<template>
  <div className="animated">
    <b-card>
      <b-card-header>
        <i class="icon-menu mr-1"></i>Chat Rooms
        <router-link :to="{ name: 'Create Chat Groups' }">
          <h5><b-badge variant="primary">Add Chat Group</b-badge></h5>
        </router-link>
      </b-card-header>
      <b-card-body style="padding : 0 1.25rem 0 1.25rem !important;">
        <v-client-table @row-click="onRowClick" :columns="columns" :data="data"  :options="options" :theme="theme" id="dataTable" delay="1500">
          <router-link slot="_id" slot-scope="props" target="_blank" :to="`chatgroups/${props.row._id}`" class="icon-eye"></router-link>
        </v-client-table>
      </b-card-body>
    </b-card>
    
  </div>

</template>

<script>
  import Vue from 'vue'
  import {ClientTable, Event} from 'vue-tables-2'
  import axios from 'axios'
  Vue.use(ClientTable)
  export default {
    name: 'dashboard',
    components: {
      ClientTable,
      Event
    },
    data: function () {
      return {
        columns: ['room_code','created_date','room_name','total_join_req','no_of_users'],
        data: [],
        joinData:[],
        options: {
          headings: {
            room_code : 'Room code',
            room_name: 'Room Name',
            total_join_req : 'Total Join Request',
            id : 'URL',
          },
          sortable: ['room_code','created_date','room_name','total_join_req','no_of_users'],
          filterable: ['room_code','created_date','room_name','total_join_req','no_of_users'],
          sortIcon: { base:'fa', up:'fa-sort-asc', down:'fa-sort-desc', is:'fa-sort' },
          pagination: {
            chunk: 5,
            edge: false,
            nav: 'scroll'
          }
        },
        useVuex: false,
        theme: 'bootstrap4',
        template: 'default'
      }
    },
    async mounted() {
     await axios.get("https://selacious-cloud-siteapi.herokuapp.com/join")
    .then(
      response => {this.joinData = response.data
        axios.get("https://selacious-cloud-siteapi.herokuapp.com/chats")
        .then(
          resp =>{
            this.data = resp.data
            for(var i=0;i<this.data.length;i++){
              var count =0;
              this.data[i].no_of_users = this.data[i].allowed_users.length
              for(var j=0;j<this.joinData.length;j++){
                if(this.data[i].room_code == this.joinData[j].room_code)
                count = count+1;
              }
              this.data[i].total_join_req = count
            }

          })
    })
    },
    methods : {
    onRowClick(event){
        window.location.href = `#/chatgroups/${event.row._id}`
      }
  }
  };
// </script>

